'use client';
import { useState, useCallback, useRef, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useEditor, EditorContent, BubbleMenu } from '@tiptap/react';
import StarterKit from '@tiptap/starter-kit';
import Link from '@tiptap/extension-link';
import Image from '@tiptap/extension-image';
import TextAlign from '@tiptap/extension-text-align';
import Underline from '@tiptap/extension-underline';
import CharacterCount from '@tiptap/extension-character-count';
import Highlight from '@tiptap/extension-highlight';
import Subscript from '@tiptap/extension-subscript';
import Superscript from '@tiptap/extension-superscript';
import TextStyle from '@tiptap/extension-text-style';
import { Color } from '@tiptap/extension-color';
import { MathJaxContext } from 'better-react-mathjax';
import { motion, AnimatePresence } from 'framer-motion';
import {
  BoldIcon,
  ItalicIcon,
  UnderlineIcon,
  ListIcon,
  ListOrderedIcon,
  AlignLeftIcon,
  AlignCenterIcon,
  AlignRightIcon,
  MinusIcon,
  ImageIcon,
  Trash2Icon,
  MoveLeftIcon,
  MoveRightIcon,
  Maximize2Icon,
  Minimize2Icon,
  LinkIcon,
  ImagePlusIcon,
  Heading1,
  Heading2,
  Heading3,
  Heading4,
  Heading5,
  Heading6,
  Pilcrow,
  ArrowLeft,
  Highlighter,
  SubscriptIcon,
  SuperscriptIcon,
  Palette,
  Undo2,
  Redo2,
  Quote,
  Code,
  Code2,
  Strikethrough,
  WrapText,
} from 'lucide-react';

const CustomImage = Image.extend({
  addAttributes() {
    return {
      ...this.parent?.(),
      width: {
        default: '100%',
        parseHTML: element => element.getAttribute('width') || '100%',
        renderHTML: attributes => ({ width: attributes.width }),
      },
      align: {
        default: 'center',
        parseHTML: element => {
          const float = element.style.float;
          return float || 'center';
        },
        renderHTML: attributes => {
          if (!attributes.align || attributes.align === 'center') return {};
          return {
            style: `float: ${attributes.align}; margin-${attributes.align === 'left' ? 'right' : 'left'}: 15px;`
          };
        },
      },
    };
  },
});

export default function EditNewsPage({ params }) {
  const router = useRouter();
  const [form, setForm] = useState({ 
    title: '', 
    content: '', 
    image: null,
    existingImage: null
  });
  const [preview, setPreview] = useState(null);
  const [mediaUrl, setMediaUrl] = useState('');
  const [mediaDialogOpen, setMediaDialogOpen] = useState(false);
  const [activeMedia, setActiveMedia] = useState(null);
  const [linkUrl, setLinkUrl] = useState('');
  const [isLinkDialogOpen, setIsLinkDialogOpen] = useState(false);
  const [textColor, setTextColor] = useState('#000000');
  const [highlightColor, setHighlightColor] = useState('#FFFF00');
  const fileInputRef = useRef(null);
  const [showSuccessPopup, setShowSuccessPopup] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  const editor = useEditor({
    extensions: [
      StarterKit.configure({
        heading: {
          levels: [1, 2, 3, 4, 5, 6],
        },
        paragraph: {
          HTMLAttributes: {
            class: 'my-2',
          },
        },
        codeBlock: {
          HTMLAttributes: {
            class: 'bg-gray-100 p-2 rounded',
          },
        },
        blockquote: {
          HTMLAttributes: {
            class: 'border-l-4 border-gray-300 pl-4 italic',
          },
        },
      }),
      Link.configure({
        openOnClick: false,
        HTMLAttributes: {
          class: 'text-blue-500 hover:underline',
        },
      }),
      TextAlign.configure({
        types: ['heading', 'paragraph', 'image'],
        alignments: ['left', 'center', 'right'],
      }),
      Underline,
      TextStyle,
      Color,
      Highlight.configure({
        multicolor: true,
      }),
      Subscript,
      Superscript,
      CustomImage.configure({
        inline: false,
        allowBase64: true,
        HTMLAttributes: {
          class: 'rounded-lg my-4 transition-all duration-200',
        },
      }),
      CharacterCount.configure({
        limit: 10000,
      }),
    ],
    content: '',
    onUpdate: ({ editor }) => {
      setForm(prev => ({ ...prev, content: editor.getHTML() }));
    },
    editorProps: {
      handleClick(view, pos, event) {
        const node = view.state.doc.nodeAt(pos);
        if (node && node.type.name === 'image') {
          setActiveMedia({
            node: node,
            pos: pos,
            attrs: node.attrs
          });
          return true;
        }
        setActiveMedia(null);
        return false;
      },
      handleDOMEvents: {
        drop: (view, event) => {
          const hasFiles = event.dataTransfer?.files?.length > 0;
          if (!hasFiles) return false;
          
          const files = Array.from(event.dataTransfer.files);
          const images = files.filter(file => /image/i.test(file.type));
          
          if (images.length === 0) return false;
          
          event.preventDefault();
          
          if (images.length === 1) {
            const file = images[0];
            const reader = new FileReader();
            reader.onload = () => {
              editor
                .chain()
                .focus()
                .setImage({ src: reader.result })
                .run();
            };
            reader.readAsDataURL(file);
          }
          return true;
        },
      },
    },
  });

  // ... (keep all your existing useEffect hooks and fetch logic)

  const setLink = useCallback(() => {
    if (!editor) return;
    
    const previousUrl = editor.getAttributes('link').href;
    setLinkUrl(previousUrl);

    setIsLinkDialogOpen(true);
  }, [editor]);

  const onLinkSubmit = useCallback(() => {
    if (!editor) return;
    
    if (linkUrl === null) {
      return;
    }

    if (linkUrl === '') {
      editor.chain().focus().extendMarkRange('link').unsetLink().run();
      return;
    }

    editor
      .chain()
      .focus()
      .extendMarkRange('link')
      .setLink({ href: linkUrl })
      .run();

    setIsLinkDialogOpen(false);
    setLinkUrl('');
  }, [editor, linkUrl]);

  const handleTextColorChange = (color) => {
    editor.chain().focus().setColor(color).run();
    setTextColor(color);
  };

  const handleHighlightColorChange = (color) => {
    editor.chain().focus().toggleHighlight({ color }).run();
    setHighlightColor(color);
  };

  // ... (keep all your existing handler functions)

  return (
    <MathJaxContext>
      <AnimatePresence>
        {showSuccessPopup && (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{ duration: 0.3 }}
            className="fixed top-4 right-4 bg-green-500 text-white px-6 py-3 rounded-lg shadow-lg z-50"
          >
            News updated successfully!
          </motion.div>
        )}
      </AnimatePresence>
      
      <div className="pb-10 px-4">
        <div className="bg-white rounded-2xl">
          <div className="flex items-center mb-6">
            <button 
              onClick={() => router.back()}
              className="mr-4 text-blue-500 hover:text-blue-700"
            >
              <ArrowLeft size={24} />
            </button>
            <h1 className="font-bold text-2xl">Edit News</h1>
          </div>
          
          <form onSubmit={handleSubmit} className="space-y-6">
            <input
              type="text"
              placeholder="Enter news title"
              className="w-full border border-gray-300 p-3 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              onChange={(e) => setForm({ ...form, title: e.target.value })}
              value={form.title}
              required
            />

            {/* Enhanced Rich Editor Toolbar */}
            {editor && (
              <>
                <div className="flex flex-wrap gap-2 border p-2 rounded-lg bg-gray-50">
                  {/* History */}
                  <button
                    type="button"
                    onClick={() => editor.chain().focus().undo().run()}
                    disabled={!editor.can().undo()}
                    className="p-2 rounded hover:bg-gray-100 disabled:opacity-50"
                    title="Undo"
                  >
                    <Undo2 size={18} />
                  </button>
                  <button
                    type="button"
                    onClick={() => editor.chain().focus().redo().run()}
                    disabled={!editor.can().redo()}
                    className="p-2 rounded hover:bg-gray-100 disabled:opacity-50"
                    title="Redo"
                  >
                    <Redo2 size={18} />
                  </button>

                  <div className="h-8 w-px bg-gray-300 mx-1"></div>

                  {/* Headings */}
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleHeading({ level: 1 }).run()}
                    className={`p-2 rounded ${editor.isActive('heading', { level: 1 }) ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Heading 1"
                  >
                    <Heading1 size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleHeading({ level: 2 }).run()}
                    className={`p-2 rounded ${editor.isActive('heading', { level: 2 }) ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Heading 2"
                  >
                    <Heading2 size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleHeading({ level: 3 }).run()}
                    className={`p-2 rounded ${editor.isActive('heading', { level: 3 }) ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Heading 3"
                  >
                    <Heading3 size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().setParagraph().run()}
                    className={`p-2 rounded ${editor.isActive('paragraph') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Paragraph"
                  >
                    <Pilcrow size={18} />
                  </button>

                  <div className="h-8 w-px bg-gray-300 mx-1"></div>

                  {/* Text Formatting */}
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleBold().run()}
                    className={`p-2 rounded ${editor.isActive('bold') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Bold"
                  >
                    <BoldIcon size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleItalic().run()}
                    className={`p-2 rounded ${editor.isActive('italic') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Italic"
                  >
                    <ItalicIcon size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleUnderline().run()}
                    className={`p-2 rounded ${editor.isActive('underline') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Underline"
                  >
                    <UnderlineIcon size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleStrike().run()}
                    className={`p-2 rounded ${editor.isActive('strike') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Strikethrough"
                  >
                    <Strikethrough size={18} />
                  </button>

                  {/* Text Color */}
                  <div className="relative group">
                    <button
                      type="button"
                      className={`p-2 rounded ${editor.isActive('textStyle') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                      title="Text Color"
                    >
                      <Palette size={18} />
                    </button>
                    <div className="absolute z-10 hidden group-hover:block bg-white p-2 rounded shadow-lg">
                      <input
                        type="color"
                        value={textColor}
                        onChange={(e) => handleTextColorChange(e.target.value)}
                        className="w-8 h-8 cursor-pointer"
                      />
                    </div>
                  </div>

                  {/* Highlight */}
                  <div className="relative group">
                    <button
                      type="button"
                      className={`p-2 rounded ${editor.isActive('highlight') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                      title="Highlight"
                    >
                      <Highlighter size={18} />
                    </button>
                    <div className="absolute z-10 hidden group-hover:block bg-white p-2 rounded shadow-lg">
                      <input
                        type="color"
                        value={highlightColor}
                        onChange={(e) => handleHighlightColorChange(e.target.value)}
                        className="w-8 h-8 cursor-pointer"
                      />
                    </div>
                  </div>

                  {/* Superscript/Subscript */}
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleSuperscript().run()}
                    className={`p-2 rounded ${editor.isActive('superscript') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Superscript"
                  >
                    <SuperscriptIcon size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleSubscript().run()}
                    className={`p-2 rounded ${editor.isActive('subscript') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Subscript"
                  >
                    <SubscriptIcon size={18} />
                  </button>

                  <div className="h-8 w-px bg-gray-300 mx-1"></div>

                  {/* Lists */}
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleBulletList().run()}
                    className={`p-2 rounded ${editor.isActive('bulletList') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Bullet List"
                  >
                    <ListIcon size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleOrderedList().run()}
                    className={`p-2 rounded ${editor.isActive('orderedList') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Numbered List"
                  >
                    <ListOrderedIcon size={18} />
                  </button>

                  {/* Block Elements */}
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleBlockquote().run()}
                    className={`p-2 rounded ${editor.isActive('blockquote') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Blockquote"
                  >
                    <Quote size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleCodeBlock().run()}
                    className={`p-2 rounded ${editor.isActive('codeBlock') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Code Block"
                  >
                    <Code2 size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().toggleCode().run()}
                    className={`p-2 rounded ${editor.isActive('code') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Inline Code"
                  >
                    <Code size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().setHorizontalRule().run()}
                    className="p-2 rounded hover:bg-gray-100"
                    title="Horizontal Line"
                  >
                    <MinusIcon size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().setHardBreak().run()}
                    className="p-2 rounded hover:bg-gray-100"
                    title="Hard Break"
                  >
                    <WrapText size={18} />
                  </button>

                  <div className="h-8 w-px bg-gray-300 mx-1"></div>

                  {/* Alignment */}
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().setTextAlign('left').run()}
                    className={`p-2 rounded ${editor.isActive({ textAlign: 'left' }) ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Align Left"
                  >
                    <AlignLeftIcon size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().setTextAlign('center').run()}
                    className={`p-2 rounded ${editor.isActive({ textAlign: 'center' }) ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Align Center"
                  >
                    <AlignCenterIcon size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => editor.chain().focus().setTextAlign('right').run()}
                    className={`p-2 rounded ${editor.isActive({ textAlign: 'right' }) ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Align Right"
                  >
                    <AlignRightIcon size={18} />
                  </button>

                  <div className="h-8 w-px bg-gray-300 mx-1"></div>

                  {/* Links & Media */}
                  <button 
                    type="button" 
                    onClick={setLink}
                    className={`p-2 rounded ${editor.isActive('link') ? 'bg-blue-100' : 'hover:bg-gray-100'}`}
                    title="Link"
                  >
                    <LinkIcon size={18} />
                  </button>
                  <button 
                    type="button" 
                    onClick={() => setMediaDialogOpen(true)}
                    className="p-2 rounded hover:bg-gray-100"
                    title="Insert Image"
                  >
                    <ImageIcon size={18} />
                  </button>
                </div>

                {/* Link Dialog */}
                {isLinkDialogOpen && (
                  <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
                    <div className="bg-white p-6 rounded-lg w-full max-w-md">
                      <h3 className="text-xl font-bold mb-4">Add Link</h3>
                      <input
                        type="text"
                        placeholder="Enter URL"
                        className="w-full border border-gray-300 p-2 rounded-lg mb-4"
                        value={linkUrl}
                        onChange={(e) => setLinkUrl(e.target.value)}
                        autoFocus
                      />
                      <div className="flex justify-end gap-2">
                        <button
                          type="button"
                          className="px-4 py-2 bg-gray-200 rounded-lg hover:bg-gray-300"
                          onClick={() => {
                            setIsLinkDialogOpen(false);
                            setLinkUrl('');
                          }}
                        >
                          Cancel
                        </button>
                        <button
                          type="button"
                          className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600"
                          onClick={onLinkSubmit}
                        >
                          Apply
                        </button>
                      </div>
                    </div>
                  </div>
                )}

                {/* Bubble Menu */}
                <BubbleMenu editor={editor} tippyOptions={{ duration: 100 }}>
                  <div className="flex bg-white shadow-lg rounded-lg p-1 border">
                    <button
                      onClick={() => editor.chain().focus().toggleBold().run()}
                      className={`p-1 rounded mx-1 ${editor.isActive('bold') ? 'bg-blue-100' : ''}`}
                    >
                      <BoldIcon size={18} />
                    </button>
                    <button
                      onClick={() => editor.chain().focus().toggleItalic().run()}
                      className={`p-1 rounded mx-1 ${editor.isActive('italic') ? 'bg-blue-100' : ''}`}
                    >
                      <ItalicIcon size={18} />
                    </button>
                    <button
                      onClick={() => editor.chain().focus().toggleUnderline().run()}
                      className={`p-1 rounded mx-1 ${editor.isActive('underline') ? 'bg-blue-100' : ''}`}
                    >
                      <UnderlineIcon size={18} />
                    </button>
                    <button
                      onClick={setLink}
                      className={`p-1 rounded mx-1 ${editor.isActive('link') ? 'bg-blue-100' : ''}`}
                    >
                      <LinkIcon size={18} />
                    </button>
                  </div>
                </BubbleMenu>
              </>
            )}

            {/* Editor Content */}
            <div className="border rounded-lg min-h-[200px] p-4 bg-white shadow-inner">
              <EditorContent editor={editor} />
            </div>

            {/* Character Count */}
            {editor && (
              <div className="text-sm text-gray-500">
                {editor.storage.characterCount.characters()} characters / {editor.storage.characterCount.words()} words
              </div>
            )}


          </form>
        </div>
      </div>
    </MathJaxContext>
  );
}